.myModal{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0,0.5);
}

.myModalContent{
    padding: 25px;
    background: rgb(255, 255, 255);
    border-radius: 16px;
    min-width: 256px;
}

.myModal.active{
    display: flex;
    justify-content: center;
    align-items: center;

}